import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import Home from '../views/Home.vue'
const View = () => import('../views/View.vue')
const Order = {
  reportList:  () => import('../views/order/enter/List.vue'),
  reportDetail:  () => import('../views/order/enter/Detail.vue')
}
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Order.reportDetail
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }, {
    path: '/order',
    name: '订单管理',
    component: View,
    children: [
      { path:'enterList', name: '录入列表', component:Order.reportList },
      { path:'enterDetail', name: '录入详情', component:Order.reportDetail },
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
