import axios from './index'
import qs from 'qs'
import { RootObject, ModuleList } from '@/model/httpObject'
import { ContentType, Method } from 'axios-mapper'

const base = {
// 	md: '/bisapi',
// 	td: '/lsapi',
// 	nm: '/deoapi/PublicMNRecord',
// 	hs: '/basapi'
// }	
	md: 'http://172.16.254.73:7004/bisapi',
	td: 'http://172.16.254.73:7000/lsapi',
	nm: '/deoapi/PublicMNRecord',
	hs: '/basapi'
}	

const module = {
	getModule: (params: any) => {
		return axios.get(`${base.md}/Module/GetModule`,  {params})
	}
}

const report = {
	//获取数据字典
	getDictionaries(params: any[]): void {
		return axios.get(`${base.md}/DataDictionary/GetTitleByDtype?dType=` + params);
	},
	//获取民事诉讼记录数据列表
	getLitigationList(params: any): void {
		return axios.get(`${base.td}/GetLitigationList?enterpriseId=${params.enterpriseId}&Mtype=${params.type}`)
	},
	getLitigationList1(params: any[]): void {
		return axios.get(`${base.td}/GetLitigationList`, qs.stringify(params))
	},
	//删除民事诉讼记录
	deleteLitigationById(params: any[]): void {
		return axios.post(`${base.td}/DeleteLitigation?plrId=${params}`)
	},
	//保存民事诉讼记录
	saveLitigation(params: any[]): void {
		return axios.post(`${base.td}/SaveLitigation`, params)
	},
	//获取民事诉讼记录备注信息
	getLitigationCommon(params: any): void {
		return axios.get(`${base.td}/GetLitigationComments?oId=${params.oId}&enterpriseId=${params.enterpriseId}&CommentType=${params.CommentType}`)
	},
	//保存民事诉讼记录备注信息
	saveLitigationCommon(params: any[]): void {
		return axios.post(`${base.td}/SaveLitigationComments`, params)
	},
	//获取负面记录数据列表
	getRecordFormList(params: any): void {
		return axios.get(`${base.nm}/GetPublicMNRecordList?enterpriseId=${params.enterpriseId}&recordType=${params.recordType}`)
	},
	//删除负面记录
	deleteRecordForm(params: any): void {
		return axios.delete(`${base.nm}/DeletePublicMNRecord?pMNId=` + params)
	},
	//保存负面记录
	saveRecordForm(params: any): void {
		return axios.post(`${base.nm}/SavePublicMNRecord`, params);
	},
	//获取记录注释
	getPublicMNRComment(params: any): void {
		return axios.get(`${base.nm}/GetPublicMNRComment?orderId=${params.orderId}&recordType=${params.recordType}`);
	},
	//保存记录注释
	savePublicMNRComment(params: any): void {
		return axios.post(`${base.nm}/SavePublicMNRComment`, params);
	},
}

const dict = {
	getDict(params: any[]): void {
		return axios.get(`${base.md}/DataDictionary/GetTitleByDtype?dType=${params}`)
	}
}

const verification = {
	//获取信息核实
	getEnterpriseCheckName(params: any) {
		return axios.get(`${base.hs}/EnterpriseCheckName/GetEnterpriseCheckName`, params)
	},
	//保存信息核实
	saveEnterpriseCheckName(params: any[]): void {
		return axios.post(`${base.hs}/EnterpriseCheckName/SaveEnterpriseCheckName`, params)
	},
}

// export const getModule = () => {
//   return http().request<RootObject<any>>('/Module/GetModule', Method.GET, undefined, ContentType.form)
// }

export default { module, report, verification, dict }